/**
 *   (Authentication Context)
 *      
 * Manages user authentication state across the entire application
 * 
 *   (Main Features):
 * - /   (Login/logout state management)
 * - JWT    (JWT token-based authentication)
 * -    (Automatic token refresh)
 * -     (Global user information access)
 * 
 *   (Developer Notes):
 * - useAuth        (Authentication state accessible anywhere via useAuth hook)
 * -      (Stores token and user info in localStorage)
 * -  JWT        (Maintains temporary authentication with local token even if backend JWT verification fails)
 */
import React, { createContext, useContext, useState, useEffect } from 'react';
import authService from '../services/authService';
import { toast } from 'react-toastify';

const AuthContext = createContext({});

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    const initAuth = async () => {
      const token = authService.getToken();
      const savedUser = authService.getCurrentUser();
      
      if (token && savedUser) {
        //        (Set as authenticated if local token exists)
        setUser(savedUser);
        setIsAuthenticated(true);
        
        try {
          //       (Try to fetch latest user info from backend)
          const response = await authService.getMe();
          if (response.success) {
            setUser(response.data);
            localStorage.setItem('user', JSON.stringify(response.data));
          }
        } catch (error) {
          console.warn('     .   .', error);
          // JWT         (Keep authenticated state even if JWT verification fails)
          //   JWT     (This is a temporary fix for backend JWT issues)
        }
      }
      setLoading(false);
    };

    initAuth();
  }, []);

  const login = async (credentials) => {
    try {
      const response = await authService.login(credentials);
      if (response.success) {
        setUser(response.data);
        setIsAuthenticated(true);
        toast.success(' ! (Login successful!)');
        return { success: true };
      } else {
        // API     (When API returns failure response)
        const message = response.message || '  (Login failed)';
        toast.error(message);
        return { success: false, message };
      }
    } catch (error) {
      //      (Network errors or other exceptions)
      const message = error.message || '    (Error during login)';
      toast.error(message);
      console.error('Login error:', error);
      return { success: false, message };
    }
  };

  const register = async (userData) => {
    try {
      const response = await authService.register(userData);
      if (response.success) {
        setUser(response.data);
        setIsAuthenticated(true);
        toast.success(' ! (Registration successful!)');
        return { success: true };
      } else {
        // API     (When API returns failure response)
        const message = response.message || '  (Registration failed)';
        toast.error(message);
        return { success: false, message };
      }
    } catch (error) {
      //      (Network errors or other exceptions)
      const message = error.message || '    (Error during registration)';
      toast.error(message);
      console.error('Registration error:', error);
      return { success: false, message };
    }
  };

  const logout = () => {
    authService.logout();
    setUser(null);
    setIsAuthenticated(false);
    toast.info(' (Logged out successfully)');
  };

  const updateProfile = async (profileData) => {
    try {
      const response = await authService.updateProfile(profileData);
      if (response.success) {
        setUser(response.data);
        toast.success('Profile updated successfully!');
        return { success: true };
      }
    } catch (error) {
      const message = error.message || 'Failed to update profile';
      toast.error(message);
      return { success: false, message };
    }
  };

  const updatePassword = async (passwordData) => {
    try {
      const response = await authService.updatePassword(passwordData);
      if (response.success) {
        toast.success('Password updated successfully!');
        return { success: true };
      }
    } catch (error) {
      const message = error.message || 'Failed to update password';
      toast.error(message);
      return { success: false, message };
    }
  };

  const value = {
    user,
    loading,
    isAuthenticated,
    login,
    register,
    logout,
    updateProfile,
    updatePassword
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};


