/**
 * VeraChain API   (VeraChain API Service Configuration)
 *  API   Axios     
 * Manages Axios instance configuration and interceptors for backend API communication
 * 
 *   (Main Features):
 * - JWT    (Automatic JWT token addition)
 * -     (Automatic authentication error handling)
 * -    (Mobile optimization settings)
 * -  HTTP /  (Global HTTP request/response handling)
 * 
 *   (Developer Notes):
 * -  API      (All API requests should use this instance)
 * -  localStorage    (Token is stored in localStorage and automatically added)
 * - 401     (Automatic logout on 401 errors)
 */
import axios from 'axios';  // HTTP   (HTTP client library)

// API  URL  (API base URL configuration)
//     URL  (Use environment variable or default server URL)
const API_URL = process.env.REACT_APP_API_URL || 'https://verachain-backend2.onrender.com/api';

// Axios     (Create and configure Axios instance)
//   HTTP   (Global HTTP client configuration)
const api = axios.create({
  baseURL: API_URL,                    //     URL (Base URL for all requests)
  timeout: 15000,                      //     15  (15-second timeout for mobile optimization)
  withCredentials: true,               //      (Include cookies and credentials)
  headers: {
    'Content-Type': 'application/json', // JSON    (JSON request body format)
    'Accept': 'application/json'        // JSON    (Accept JSON response format)
  },
  //      keep-alive  (Connection keep-alive for mobile performance)
  httpAgent: false,                    // HTTP agent  (Disable HTTP agent)
  httpsAgent: false,                   // HTTPS agent  (Disable HTTPS agent)
  maxRedirects: 3                      //  3   (Allow maximum 3 redirects)
});

//    (Request interceptor configuration)
//  API      (Middleware that runs automatically before all API requests)
api.interceptors.request.use(
  (config) => {
    // localStorage JWT   (Get JWT token from localStorage)
    const token = localStorage.getItem('token');
    
    //   Authorization   (Add to Authorization header if token exists)
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;  // Bearer    (Add in Bearer token format)
    }
    
    return config;  //    (Return modified config)
  },
  (error) => {
    //       (Handle errors during request configuration)
    return Promise.reject(error);
  }
);

//    (Response interceptor configuration)
//  API       (Middleware that runs automatically after receiving all API responses)
api.interceptors.response.use(
  (response) => response,  //     (Pass through successful responses)
  (error) => {
    // 401 Unauthorized   (Handle 401 Unauthorized errors)
    //       (Occurs when token is expired or invalid)
    if (error.response?.status === 401) {
      console.warn('JWT    -      (JWT token verification failed - possible backend auth issue)', error.response?.data);
      
      //  :     (Temporary measure: auto-logout only for specific endpoints)
      // /auth/login, /auth/register    (Remove token only on /auth/login, /auth/register failures)
      const isAuthEndpoint = error.config?.url?.includes('/auth/login') || error.config?.url?.includes('/auth/register');
      
      if (isAuthEndpoint) {
        localStorage.removeItem('token');  // JWT   (Remove JWT token)
        localStorage.removeItem('user');   //    (Remove user info)
        window.location.href = '/login';   //    (Redirect to login page)
      }
      //        (For other protected routes, just log error and continue)
    }
    
    //        (Pass through all errors for individual component handling)
    return Promise.reject(error);
  }
);

//  API    (Export configured API instance as default)
//    import  (Import and use in other service files)
// 
//    (Important Temporary Modification):
// -  JWT    401    (401 error handling relaxed due to backend JWT verification issues)
// -    JWT     (Distinguishes between actual auth failures and JWT verification issues)
// -   JWT        (Needs restoration to original logic after backend JWT issues are resolved)
export default api;


