/**
 * VeraChain    (Main Application Component)
 * React       
 * Root component of React application managing routing and global configurations
 * 
 *   (Main Features):
 * - React Router  SPA  (SPA routing via React Router)
 * -     (User authentication context provision)
 * -    (Privacy protection initialization)
 * -     (Global toast notification setup)
 * -   lazy loading (Code splitting and lazy loading)
 * 
 *   (Developer Notes):
 * -    lazy loading  (All screen components loaded via lazy loading)
 * - PrivateRoute     (Protected pages with PrivateRoute)
 * -     (Console logs disabled in production)
 */
import React, { useEffect, Suspense } from 'react';                    // React   (React core library)
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'; // SPA  (SPA routing)
import { ToastContainer } from 'react-toastify';                      //   (Notification toast)
import 'react-toastify/dist/ReactToastify.css';                       //   (Toast styles)
import { AuthProvider } from './context/AuthContext';                 //    (Authentication context provider)
import PrivateRoute from './components/common/PrivateRoute';          //    (Authentication protected route)
import LoadingSpinner from './components/common/LoadingSpinner';      //    (Loading spinner component)
import { initializePrivacy } from './utils/privacyUtils';             //    (Privacy protection utility)
import './styles/theme.css';                                           //    (Global theme styles)
import './styles/App.css';                                             //   (Application styles)

//         (Lazy load screens for better mobile performance)
//           (Reduce initial bundle size via code splitting, load only when needed)
const LoginScreen = React.lazy(() => import('./components/screens/LoginScreen'));           //   (Login screen)
const RegisterScreen = React.lazy(() => import('./components/screens/RegisterScreen'));     //   (Registration screen)
const MainScreen = React.lazy(() => import('./components/screens/MainScreen'));             //   (Main dashboard)
const ProfileScreen = React.lazy(() => import('./components/screens/ProfileScreen'));       //    (Profile management screen)
const ScanScreen = React.lazy(() => import('./components/screens/ScanScreen'));             // QR /   (QR code/product scan screen)
const CertificatesScreen = React.lazy(() => import('./components/screens/CertificatesScreen')); //    (Certificates list screen)
const CertificateDetail = React.lazy(() => import('./components/screens/CertificateDetail')); //    (Certificate detail screen)

/**
 *  App   (Main App functional component)
 *       
 * Serves as application entry point and initializes global configurations
 */
function App() {
  //    (Application initialization effect)
  //       (Settings executed only once when app loads)
  useEffect(() => {
    //     (Initialize privacy protection system)
    //   ,  ,      (Includes user data protection, tracking blocking, security headers setup)
    initializePrivacy();
    
    //      (Disable console logs in production)
    //         (For performance improvement and preventing sensitive information exposure)
    if (process.env.NODE_ENV === 'production') {
      console.log = () => {};    //    (Disable general logs)
      console.warn = () => {};   //    (Disable warning logs)
      console.error = () => {};  //    (Disable error logs)
      console.info = () => {};   //    (Disable info logs)
    }
  }, []); //      (Empty dependency array to run only on mount)

  return (
    //      (Wrap entire application with authentication context)
    //         (All child components can access user authentication state)
    <AuthProvider>
      {/* React Router SPA   (Set up SPA routing with React Router) */}
      <Router>
        <div className="App">
          {/* Suspense lazy loading     (Manage loading state of lazy loading components with Suspense) */}
          <Suspense fallback={<LoadingSpinner />}>
            <Routes>
              {/*   -     (Public routes - accessible without authentication) */}
              <Route path="/login" element={<LoginScreen />} />        {/*   (Login page) */}
              <Route path="/register" element={<RegisterScreen />} />  {/*   (Registration page) */}
              {/*     (Protected routes requiring authentication) */}
              {/* PrivateRoute  JWT     /  (PrivateRoute component checks JWT token to allow/deny access) */}
              <Route 
                path="/" // /  (Home/dashboard route)
                element={
                  <PrivateRoute>
                    <MainScreen /> {/*   -  , ,    (Main dashboard - shows product scans, stats, recent activity) */}
                  </PrivateRoute>
                } 
              />
              <Route 
                path="/scan" //    (Product scan route)
                element={
                  <PrivateRoute>
                    <ScanScreen /> {/* QR /   - AI   (QR code/camera scan screen - AI authenticity verification) */}
                  </PrivateRoute>
                } 
              />
              <Route 
                path="/certificates" //    (Certificates list route)
                element={
                  <PrivateRoute>
                    <CertificatesScreen /> {/*      (Display all user's certificates) */}
                  </PrivateRoute>
                } 
              />
              <Route 
                path="/certificate/:id" //     (Individual certificate detail route)
                element={
                  <PrivateRoute>
                    <CertificateDetail /> {/*    , NFT ,   (Detailed info, NFT data, verification history of specific certificate) */}
                  </PrivateRoute>
                } 
              />
              <Route 
                path="/profile" //    (User profile route)
                element={
                  <PrivateRoute>
                    <ProfileScreen /> {/*  , ,    (Profile management, settings, account info editing) */}
                  </PrivateRoute>
                } 
              />
            </Routes>
          </Suspense>
          {/*     (Global toast notification container) */}
          {/*    , ,    (Displays success, error, info messages from across the app) */}
          <ToastContainer 
            position="top-right"      //     (Display at top-right of screen)
            autoClose={3000}          // 3    (Auto close after 3 seconds)
            hideProgressBar={false}   //    (Show progress bar)
            newestOnTop={false}       //     (Show new notifications at bottom)
            closeOnClick              //    (Enable close on click)
            rtl={false}               // ->   (Left-to-right text direction)
            pauseOnFocusLoss          //     (Pause when focus is lost)
            draggable                 //   (Enable dragging)
            pauseOnHover              //    (Pause on mouse hover)
            theme="dark"              //    (Apply dark theme)
          />
        </div>
      </Router>
    </AuthProvider>
  );
}

//     (Export as root component of the application)
export default App;



